@import url(https://fonts.googleapis.com/css?family=Muli);
@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

li {
  margin-bottom: 5px;
}
ul{
  margin-top: 5px;
}
.content {
  /* position: relative; */
  margin-left: auto;
  margin-right: auto;
  padding: 0.01em 16px;
  max-width: 1370px;
}
.col {
  float: left;
  padding: 0 8px;
}
.col.half {
  -webkit-column-count: 2;
          column-count: 2;
  float: left;
  width: 50%;
  padding: 0 8px;
}
@media screen and (max-width: 600px) {
  .col {
    width: 100%;
  }
}
.row.center {
  display: flex;
  align-items: center;
  justify-content: center;
}
.row:before,
.row:after {
  content: "";
  display: table;
  clear: both;
}
/* Padding */
.padding12 {
  padding: 12px 24px !important;
}
.padding32 {
  padding-top: 32px !important;
  padding-bottom: 32px !important;
}
.padding64 {
  padding-top: 64px !important;
  padding-bottom: 64px !important;
}
/* Images */
#profile {
  width: 529px;
  height: 774px;
}
#oldwell {
  width: 144px;
  height: 211px;
}
#jmp {
  width: 150px;
  height: 150px;
}
/* Divider */
divider {
  width: 100%;
  height: 2px;
  display: block;
  background: #B5B3B3;
}

/* Nav Bar*/
.navBar {
  position: -webkit-sticky;
  position: sticky;
  width: 100%;
  height: 153px;
  left: 0px;
  top: 0px;
  background: white;
  z-index: 9999;
  transition: all 0.3s;
}
.navBar a#button, .navBar a#title {
  transition: all 0.3s;
}
.navBar.smaller {
  height: 78px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.navBar.smaller a#title {
  height: 47px;
  width: 240px;
  left: 37px;
  top: 17px;
  font-weight: 600;
  font-size: 36px;
  line-height: 45px;
}
/* fix left align */
.navBar.smaller a#button {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: 30px;
  left: 844px;
  top: 22px;
  font-weight: normal;
  font-size: 24px;
  line-height: 30px;
  color: #000000;
}
.navTitle {
  display: inline-block;
  position: absolute;
  width: 494px;
  height: 102px;
  left: 35px; top: 25px;
  text-decoration: none;
  color: black;
}
.navButton {
  display: inline-block;
  position: absolute;
  height: 43px;
  top: 57px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  text-decoration: none;
  color: black;
}
.navButton::after {
  content: "";
  display: block;
  width: 0;
  height: 2px;
  background: black;
  transition: width 0.3s;
}
.navButton:hover::after {
  width: 100%;
}
/* figure out underline on active page */
.navButton.active {
  content: "";
  display: block;
  width: 0;
  height: 2px;
  background: black;
  width: 100%;
}
@media screen and (max-width: 580px) {
  #navBar {
    padding: 20px 10px !important;
  }
}

